.product {
    margin: 50px 25px;
    .feature-title {
      font-family: "Open Sans",sans-serif;
      font-size: 24px;
      color: #253237;
    }
    .feature-description {
      color: #888;
      font-size: 14px;
      font-weight: 400;
      line-height: 18px;
      position: relative;
      padding: 15px 0px;
    }
    .banner-box {
      .banner-image {
        padding-right: 50px;
      }
    }
    .product-list {
      margin-top: 10px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .product-item {
        border: 1px solid #ebebeb;
        border-radius: 10px;
        flex-basis: 23%;
        padding: 15px;
        margin-bottom: 2.5%;
        display: flex;
        &:hover {
          border-color: #4fb78d;
        }
        .product-image {
          height: 120px;
          width: 120px;
          &:hover{
            cursor: pointer;
        }
        }
        .product-description {
          padding: 25px 5px 5px;
          .category {
            font-size: 12px;
            text-transform: uppercase;
            color: #999;
            line-height: 1;
            display: block;
            padding-bottom: 5px;
          }
          .name {
            line-height: 18px;
            display: block;
            font-weight: 700;
            padding-bottom: 5px;
            white-space: nowrap; 
            width: 130px; 
            overflow: hidden;
            text-overflow: ellipsis;
            &:hover{
              opacity: 0.5;
              cursor: pointer;
              color:#4fb78d;
          } 
          }
          .star {
            .grey-star {
              height: 15px;
              width: 20px;
            }
            .yellow-star {
              height: 15px;
              width: 20px;
            }
          }
          .staricon {
            padding-bottom: 5px;
            color: #d1cfd0;
            &.active {
              color: #fdd836;
            }
          }
          .price {
            display: flex;
            .price-original {
              padding-right: 10px;
              color: #555;
              font-size: 16px;
              &.active {
                text-decoration: line-through;
              }
            }
            .price-sale {
              color: #de6764;
            }
          }
        }
      }
    }
}